import React, { useState, useEffect } from "react";
import "bootstrap/dist/css/bootstrap.css";
import { useParams } from "react-router-dom";
import { Figure, Button } from "react-bootstrap"
import { useHistory } from "react-router-dom";

function Quiz(props) {
  const history = useHistory();
  const [flowers, setFlowers] = useState([]);
  const [score, setScore] = useState(0);
  const [questionsTried, setQuestionsTried] = useState([false, false, false, false, false, false]);
  const quizid = useParams().id
  useEffect(function () {
    async function getFlowerQuiz() {
      const response = await fetch('https://brettbookbinder-imagequiz.herokuapp.com/quiz/' + quizid)
      const data = await response.json()
      setFlowers(data)
    }
    getFlowerQuiz()
  }, [])

  async function submitScore() {
    const response = await fetch('https://brettbookbinder-imagequiz.herokuapp.com/score/', {
      method: "POST",
      body: JSON.stringify({
        customerid: localStorage.getItem('customerid'),
        quizid: quizid,
        score: score,
      }),
      headers: {
        'Content-Type': 'application/json'
      }
    })
    const data = await response.json()
    console.log(data)
    alert('Score Submitted!')
    history.push('/');
  }

  return (
    <div>
      <h2>Score: {score}</h2>
      {
        flowers.map(function (flower, index) {
          const choice1 = flower.choices.split(' ')[0]
          const choice2 = flower.choices.split(' ')[1]
          const choice3 = flower.choices.split(' ')[2]
          return (
            <div>
              {
                questionsTried[index] === false &&
                <Figure>
                  <Figure.Image src={flower.picture} />
                  <Button onClick={function () {
                    if (choice1 === flower.answer) {
                      setScore(score + 1)
                    }
                    const copy = questionsTried.slice()
                    copy[index] = true
                    setQuestionsTried(copy)
                  }}>{choice1}</Button>
                  <Button onClick={function () {
                    if (choice2 === flower.answer) {
                      setScore(score + 1)
                    }
                    const copy = questionsTried.slice()
                    copy[index] = true
                    setQuestionsTried(copy)
                  }}>{choice2}</Button>
                  <Button onClick={function () {
                    if (choice3 === flower.answer) {
                      setScore(score + 1)
                    }
                    const copy = questionsTried.slice()
                    copy[index] = true
                    setQuestionsTried(copy)
                  }}>{choice3}</Button>
                </Figure>
              }
            </div>

          )
        })
      }
      <div>
        {questionsTried.every(function (current) {
          if (current === true) {
            return true
          }
        }) && <div> <Button onClick={function () {
          submitScore()
        }} disabled={localStorage.getItem('username') ? false : true}>Submit Score</Button>
            <Button onClick={function () {
              setScore(0)
              setQuestionsTried([false, false, false, false, false, false])
            }}>Reset Quiz</Button></div>
        }
      </div>
    </div>
  )
}
export default Quiz;
