//import flowers from "../flowers";
import Figure from "react-bootstrap/Figure";
import "bootstrap/dist/css/bootstrap.css";
import { Link } from "react-router-dom";
import React, { useState, useEffect } from "react";


function Home() {
  const [flowers, setFlowers] = useState([]);
  useEffect(function () {
    async function getFlowerImages() {
      const response = await fetch('https://brettbookbinder-imagequiz.herokuapp.com/images')
      const data = await response.json()
      setFlowers(data)
    }
    getFlowerImages()
  }, [])
  return (
    <div>
      {
        flowers.map(function (flower, index) {
          const flowerid = index + 1
          return (
            <Figure key={flowerid}>
              <Link
                to={`/quiz/${flowerid}`}>
                <Figure.Image
                  width={200}
                  height={200}
                  alt={flower.name}
                  src={flower.picture}
                />
                <Figure.Caption>{flower.name}</Figure.Caption>
              </Link>
            </Figure>
          )
        })
      }
    </div>
  );
}

export default Home;
